body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

  /*********************************************************************************/
  /* Media 375px                                                                   */
  /*********************************************************************************/
  @media (min-width: 375px) {
    /*<-------------------------Element For all Page------------------------------>*/

    * {
        box-sizing: border-box;
    }

    body {
        height: 100%;
        margin: 0px;
        padding: 0px;
        background-color: white;
        font-family: 'Open Sans', sans-serif;
        font-size: 11pt;
        font-weight: 300;
        color: #6c6c6c;
    }

    p {
        line-height: 180%;
        font-size: 12pt;
        width: 80%;
        word-wrap: break-word;
    }

    h1 {
        margin: 0.15em;
        padding: 0;
        line-height: 180%;
        font-weight: 600;
        color: #454445;
    }

    h2 {
        margin-top: 0.5em;
        margin-bottom: 0.5em;
        padding: 0;
        font-weight: 600;
        color: #454445;
    }

    h3 {
        margin: 0.3em;
        padding-top: 0.3em;
        font-weight: 300;
        font-size: 1em;
        color: #454445;
        text-align: center;
    }

    a:hover {
        text-decoration: none;
    }

    #wrapper {
        background: #FFF;
    }

    .row, .result {
        margin-left: 15px;
        margin-right: 15px;
    }

    ol, ul {
        padding: 0;
        list-style: none;
    }

    /*<-------------------------All Container------------------------------>*/
    .list-container {
        padding: 0.5em;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }

    .container {
        padding: 5.5em;
    }

    .flex-container {
        margin: auto;
        width: 100%;
        flex-wrap: wrap;
        height: 100%;
        text-align: center;
        padding: 0 0px;
    }

    .flex-item-1 {
        margin-top: 30px;
        margin-bottom: 10px;
    }

    .flex-item-2 {
        margin-bottom: 10px;
    }

    .flex-item-3 {
        margin-top: 30px;
        margin-bottom: 30px;
    }

    /*<-------------------------Footer------------------------------>*/
    footer {
        background-color: #3b945e;
    }

    .copyright {
        overflow: hidden;
    }

    .copyright p {
        letter-spacing: 0.15em;
        text-align: center;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 0.9em;
        color: white;
        width: 100%;
    }

    .copyright a {
        color: white;
    }

    .close {
        text-align: center;
        height: 1.15em;
        width: 1.15em;
        border-radius: 50px;
        position: relative;
        background-color: white;
        color: 3b945e;
        top: -12px;
        left: -30px;
        border: 1px solid lightgray;
        font-weight: 600;
    }

    .close:hover {
        background-color: #3b945e;
        color: white;
        cursor: pointer;
    }

    /*<------------------------------------Header------------------------------>*/
    #header {
        position: relative;
        padding-bottom: 4em;
        padding-top: 4em;
        height: auto;
        text-align: center;
    }

    #logo {
        position: relative;
        bottom: 2em;
        width: 100%;
    }

    #logo h1 {
        display: inline-block;
        font-size: 2.2em;
        text-transform: uppercase;
        font-weight: 700;
        color: #FFF;
    }

    #logo a {
        text-decoration: none;
        color: #FFF;
    }

    /*<---------------------------------------Menu------------------------------>*/
    #menu {
        position: absolute;
        width: 100%;
    }

    #menu ul {
        list-style-type: none;
        overflow: hidden;
        width: 100%;
    }

    #menu li {
        display: inline-block;
        text-align: center;
        width: 49%;
    }

    #menu .current_page_item {
        background: #3b945e;
        color: #FFF;
    }

    /*<-------------------------app description/people image------------------------------>*/
    .flex-item-1, .flex-item-2, #pg1 {
        align-content: center;
        width: 100%;
        align-items: center;
    }

    .app-description {
        text-align: left;
        align-content: center;
        width: 100%;
        padding: 2em;
    }

    .articleLink {
        color: #068900;
    }

    #people {
        width: 100%;
        background-position: top;
        background-size: contain;
        background-repeat: no-repeat;
        background-color: white;
        align-content: center;
        align-self: center;
        padding: 2em;
    }

    /*<-------------------------Activity Center------------------------------>*/
    .categorylink {
        color: white;
    }

     /*<-------Search Box-------->*/
    .flex2-item-1 {
        align-content: right;
        width: 100%;
    }

    .form-inline {
        justify-content: center;
        width: 100%;
    }

    input[type="text"] {
        padding: 10px;
        font-size: 16px;
        border: 1px solid #81cc9e;
        border-width: 3px;
        float: left;
        width: 91%;
        height: 40px;
        border-bottom-left-radius: 8px;
        border-top-left-radius: 8px;
    }

    button[type="submit"] {
        float: left;
        outline: none;
        width: 9%;
        height: 40px;
        padding: 5px;
        background: #81cc9e;
        color: white;
        font-size: 17px;
        border-left: none;
        cursor: pointer;
        border-bottom-right-radius: 8px;
        border-top-right-radius: 8px;
        border: 1px solid #81cc9e;
        align-content: center;
    }

    .fa.fa-search {
        zoom: 1;
        vertical-align: center;

    }

    button[type="submit"]:hover {
        background: #3b945e;
    }

    #submit {
        margin-right: 0;
    }

    #searchBar {
        margin-bottom: 65px;
    }

    /*<-------Category Selection-------->*/

    .btn-group {
        cursor: pointer;
        width: 100%;
    }

    .menuButton {
        background-color: #81cc9e;
        border-color: #81cc9e;
        border-style: solid;
        color: white;
        font-size: 55%;
        height: 40px;
        width: 20%;
        font-weight: bolder;
    }

    #option1 {
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
    }

    #option5 {
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
    }

    .active {
        background-color: #3b945e !important;
        color: white;
    }

    .menuButton:hover {
        background-color: #3b945e !important;
        color: white;
    }

    .activityIcon {
        width: 30px;
        height: 30px;
        margin: 0.5em;
    }

    .activityList {
        background-color: lightgrey;
        height: auto;
        width: 100%;
        margin-bottom: 50px;
        margin-top: 10px;
        border-radius: 8px;
    }

    #pg2-1, #pg2-2 {
        align-content: center;
        align-items: center;
        width: 100%;
        padding: 0;
    }

    .flex-activity-item {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        transition: 0.2s;
        height: 45px;
        margin: 0.5rem;
        background-color: white;
        border-radius: 8px;
        display: block;
        align-items: center;
    }

    .flex2-item-1-1 {
        align-content: center;
        align-items: center;
    }

    .flex-activity-item:hover {
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
        background-color: #81cc9e;
    }

    .flex2-item-1 h2 {
        text-align: center;
    }

    .flex2-item-1 p {
        text-align: center;
        width: 100%;
    }

    #allRows {
        width: 100%;
        justify-content: center;
        margin: 0 auto;
    }

    /*<-------------------------My Activity------------------------------>*/
    .dropzone {
        background-color: lightgrey;
        height: auto;
        width: 100%;
        margin-bottom: 20px;
        margin-top: 10px;
        border-radius: 8px;
    }

    .my-activity-row {
        width: 100%;
        justify-content: center;
        align-content:center;
        margin: 0 0;
    }

    .flex3-item1-1 {
        width: 100%;
        justify-content: center;
        align-content: center;
        margin: 0 auto;
    }

    section.flex3-item-1 p {
        width: 100%;
    }

    .flex-my-activity-item {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        transition: 0.2s;
        width: 100%;
        height: 125px;
        margin:0.5rem;
        background-color: white;
        border-radius: 8px;
        display: block;
        justify-content: center;
    }

    p.calculation-description {
        text-align: left;
        font-size: 13px;
        margin-top: 5px;
        width: 100%;
    }

    div.col.button-col {
        text-align: right;
    }

    .calculator-row {
        width: 92%;
        align-content: center;
        justify-content:center;
    }

    #calculate-btn {
        width: 100%;
        margin-bottom: 30px;
        justify-content: center;
        align-content: center;
    }

    #calculate {
        margin-top: 0.3em;
        height: 43px;
        width: 100%;
    }

    .col-3.icon{
        padding:0;
    }

    /*<-------------------------Result------------------------------>*/
    .result {
        background-color: lightgrey;
        height: 540px;
        border-radius: 8px;
        margin-top: 20px;
        margin-bottom: 50px;
        justify-content: center;
        align-content: center;
    }

    #carbon-level-title {
        padding-top: 25px;
    }

    #carbon-level-low {
        /* color should follow carbon level color */
        color: #63a64d;
        margin-top: 10px;
    }

    #carbon-level-low-moderate {
        /* color should follow carbon level color */
        color: #ffc106;
        margin-top: 10px;
    }

    #carbon-level-moderate {
        /* color should follow carbon level color */
        color: orange;
        margin-top: 10px;
    }

    #carbon-level-moderate-high {
        /* color should follow carbon level color */
        color: #f56733;
        margin-top: 10px;
    }

    #carbon-level-high {
        /* color should follow carbon level color */
        color: #dc3546;
        margin-top: 10px;
