        width: 100%;
    }

    .contain-result {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        transition: 0.2s;
        width: 85%;
        height: 210px;
        margin: 0.7rem;
        background-color: white;
        border-radius: 8px;
        display: block;
        margin-left: 10%;
    }

    .progress-bar-container {
        margin-top: 30px;
        margin-left: 5px;
        margin-right: 5px;
    }

    div#moderate-high.progress-bar {
        background-color: #f56733 !important;
        height: 30px !important;
        font-size:7px !important;
        width: 20%;
    }

    div#moderate.progress-bar {
        background-color: orange !important;
        height: 30px !important;
        font-size:7px !important;
        width: 20%;
    }

    div#high.progress-bar {
        height: 30px !important;
        font-size:7px !important;
        width: 20%;
    }

    div#low.progress-bar {
        height: 30px !important;
        background-color: #63a64d !important;
        font-size:7px !important;
        width: 20%;
    }

    div#low-moderate.progress-bar {
