    }

    .lowcarbonArrow {
        width: 0;
        height: 25;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid transparent;
        animation-duration: 2.5s;
        animation-name: slideinLow;
        animation-iteration-count: 1;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
    }

    @keyframes slideinLow {
        from {
          margin-left: 0%;
          margin-left:0;
          width: 0;
          border-bottom: 15px solid #63a64d;
        }

        to {
          margin-right: 25%;
          margin-left:8.5%;
          width: 0;
          border-bottom: 15px solid #63a64d;
        }
    }

    .lowModeratecarbonArrow {
        width: 0;
        height: 25;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid transparent;
        animation-duration: 2.5s;
        animation-name: slideinlowM;
        animation-iteration-count: 1;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
    }

    @keyframes slideinlowM {
