        }

        to {
          margin-right: 100%;
          margin-left:28%;
          width: 0;
          border-bottom: 15px solid #ffc106;
        }
    }

    .moderatecarbonArrow {
        width: 0;
        height: 25;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid transparent;
        animation-duration: 2.5s;
        animation-name: slideinMod;
        animation-iteration-count: 1;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
    }

    @keyframes slideinMod {
        from {
          margin-left: 0%;
          margin-left:0;
          width: 0;
          border-bottom: 15px solid #63a64d;
        }

        to {
          margin-right: 100%;
          margin-left:47%;
          width: 0;
          border-bottom: 15px solid orange;
        }
    }
    
    .moderateHighcarbonArrow {
        width: 0;
        height: 25;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid transparent;
        animation-duration: 2.5s;
        animation-name: slideinModH;
        animation-iteration-count: 1;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
    }

    @keyframes slideinModH {
        from {
