          width: 0;
          border-bottom: 15px solid #63a64d;
        }

        to {
          margin-right: 100%;
          margin-left: 66.5%;
          width: 0;
          border-bottom: 15px solid #f56733;
        }
    }

    .highcarbonArrow {
        width: 0;
        height: 25;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid transparent;
        animation-duration: 2.5s;
        animation-name: slideinH;
        animation-iteration-count: 1;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
    }

    @keyframes slideinH {
        from {
          margin-left: 0%;
          width: 0;
          border-bottom: 15px solid #63a64d;
        }

        to {
          margin-right: 100%;
          margin-left: 84%;
          width: 0;
          border-bottom: 15px solid #dc3546;
        }
    }

    /*<-------------------------About Page------------------------------>*/
    .profile {
        height: 12em;
        width: 12em;
        background-repeat: no-repeat;
        background-color: white;
        margin-top: 25px;
        margin-bottom: 15px;
    }

    .name {
        margin-bottom: 10px;
        font-size: 20pt;
    }

    .list {
        font-size: 13pt;
    }

    .col {
        text-align: center;
        padding-left: 0em;
        margin-top: 0.3em;
    }

    .contact {
        height: 30px;
        width: 30px;
        margin-bottom: 15px;
    }

    .flex-item-4 {
        margin-top: 30px;
        margin-bottom: 30px;
        width: 100%;
        margin-left: 0;
    }

    .flex-item-5 {
        margin-top: 30px;
        margin-bottom: 30px;
        width: 100%;
        margin-left: 0;
    }

    .flex-item-6 {
        margin-top: 30px;
        margin-bottom: 30px;
        width: 100%;
        margin-left: 0;
    }

    .pg1-about, .info {
        justify-content: center;
        align-content: center;
    }

    /*<-------------------------Additional Menu Items------------------------------>*/
    #menu li, #menu li span {
        cursor: pointer;
        display: inline-block;
        outline: none;
        letter-spacing: 0.20em;
        width: 50%;
        line-height: 57px;
        text-decoration: none;
        font-size: 1em;
        font-weight: 600;
        text-transform: uppercase;
        color: #FFF;
    }

    #menu li:hover,
    #menu li.active,
    #menu li.active span {
        background: #202d45;
        color: #FFF;
    }
}


